.sideBar {
  height: 100vh;
  width: 14rem;

  img {
    filter: invert(100%);
    width: 1.5rem;
  }
}

.item {
  display: flex;
  align-items: center;
  border-radius: 10px;
  width: 90%;
  height: 2.6rem;
  margin-top: 0.5rem;
  cursor: pointer;

  img {
    width: 1.3rem;
    fill: white;
    cursor: pointer;
    font-size: 1px;
    margin: 0 1rem;
  }

  h1 {
    font-size: 0.9rem;
    font-weight: 600;
    color: white;
  }
  &:hover {
    background-color: #3f3f3f80;
    border-radius: 10px;
  }
}

.active {
  background-color: #3f3f3fad;
  border-radius: 10px;
}

.border {
  border-bottom: 1px solid #3f3f3fad;
  padding-bottom: 1rem;
  padding-left: 0.5rem;
}

/* on click */

.clicked_sideBar {
  width: 4.5rem;
}

.clicked_item {
  display: flex;
  flex-direction: column;
  padding: 0.8rem 0.2rem;
  h1 {
    font-size: 0.7rem;
  }
}

.clicked_active {
  padding: 0.8rem 0.2rem;
}
